/***********
1. Fonts
***********/

/*********************************
2. Body and some general stuff
*********************************/

* {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -webkit-text-shadow: rgba(0, 0, 0, 0.01) 0 0 1px;
  text-shadow: rgba(0, 0, 0, 0.01) 0 0 1px;
}
body {
  font-family: "Roboto", sans-serif;
  font-size: 14px;
  font-weight: 400;
  background: #ffffff;
  color: #a5a5a5;
}
div {
  display: block;
  position: relative;
  box-sizing: border-box;
}
ul {
  list-style: none;
  margin-bottom: 0px;
}
p {
  font-family: "Roboto", sans-serif;
  font-size: 14px;
  line-height: 1.85;
  font-weight: 400;
  color: #76777a;
  -webkit-font-smoothing: antialiased;
  -webkit-text-shadow: rgba(0, 0, 0, 0.01) 0 0 1px;
  text-shadow: rgba(0, 0, 0, 0.01) 0 0 1px;
}
p a {
  display: inline;
  position: relative;
  color: inherit;
  border-bottom: solid 1px #ffa07f;
  -webkit-transition: all 200ms ease;
  transition: all 200ms ease;
}
p:last-of-type {
  margin-bottom: 0;
}
a,
a:hover,
a:visited,
a:active,
a:link {
  text-decoration: none;
  -webkit-font-smoothing: antialiased;
  -webkit-text-shadow: rgba(0, 0, 0, 0.01) 0 0 1px;
  text-shadow: rgba(0, 0, 0, 0.01) 0 0 1px;
}
p a:active {
  position: relative;
  color: #ff6347;
}
p a:hover {
  color: #ffffff;
  background: #ffa07f;
}
p a:hover::after {
  opacity: 0.2;
}
h1 {
  font-size: 48px;
}
h2 {
  font-size: 36px;
}
h3 {
  font-size: 24px;
}
h4 {
  font-size: 18px;
}
h5 {
  font-size: 14px;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Roboto Slab", serif;
  font-weight: 700;
  -webkit-font-smoothing: antialiased;
  -webkit-text-shadow: rgba(0, 0, 0, 0.01) 0 0 1px;
  text-shadow: rgba(0, 0, 0, 0.01) 0 0 1px;
  color: #384158;
  margin-bottom: 0;
}
.form-control {
  color: #db5246;
}
section {
  display: block;
  position: relative;
  box-sizing: border-box;
}
.clear {
  clear: both;
}
.clearfix::before,
.clearfix::after {
  content: "";
  display: table;
}
.clearfix::after {
  clear: both;
}
.clearfix {
  zoom: 1;
}
.float_left {
  float: left;
}
.float_right {
  float: right;
}
.trans_200 {
  -webkit-transition: all 200ms ease;
  transition: all 200ms ease;
}
.trans_300 {
  -webkit-transition: all 300ms ease;
  transition: all 300ms ease;
}
.trans_400 {
  -webkit-transition: all 400ms ease;
  transition: all 400ms ease;
}
.trans_500 {
  -webkit-transition: all 500ms ease;
  transition: all 500ms ease;
}
.fill_height {
  height: 100%;
}
.super_container {
  width: 100%;
  overflow: hidden;
}
.prlx_parent {
  overflow: hidden;
}
.prlx {
  height: 130% !important;
}
.parallax-window {
  min-height: 400px;
  background: transparent;
}
.nopadding {
  padding: 0px !important;
}

/*********************************
3. Header
*********************************/

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
  box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.05);
  -webkit-transition: all 200ms ease;
  transition: all 200ms ease;
}
.header.scrolled {
  top: -40px;
}

/*********************************
3.1 Top Bar
*********************************/

.top_bar {
  width: 100%;
  background: #14bdee;
}
/* .header.scrolled .top_bar {
} */
.top_bar_container {
  width: 100%;
  height: 100%;
}
.top_bar_content {
  width: 100%;
  height: 40px;
}
.top_bar_contact_list li {
  display: inline-block;
}
.question {
  font-family: "Roboto Slab", serif;
  font-size: 14px;
  color: #ffffff;
  margin-left: 0px;
}
.top_bar_contact_list li i,
.top_bar_contact_list li > div {
  display: inline-block;
  font-size: 14px;
  color: #ffffff;
}
.top_bar_contact_list li > div {
  margin-left: 1px;
}
.top_bar_contact_list li:not(:last-child) {
  margin-right: 21px;
}
.top_bar_login {
  height: 100%;
}
.login_button {
  width: 160px;
  height: 100%;
  background: #f3f3f3;
  text-align: center;
}
.login_button a {
  display: block;
  font-size: 14px;
  font-weight: 500;
  line-height: 40px;
  color: #384158;
}

/*********************************
3.2 Header Content
*********************************/

.header_container {
  width: 100%;
  background: #ffffff;
}
.header_content {
  height: 90px;
  -webkit-transition: all 200ms ease;
  transition: all 200ms ease;
}
.header.scrolled .header_content {
  height: 80px;
}

/*********************************
3.3 Logo
*********************************/

.logo,
.logo_text {
  display: inline-block;
}
.logo {
  width: 60px;
  height: 60px;
  -webkit-transition: all 200ms ease;
  transition: all 200ms ease;
}
.logo img {
  max-width: 100%;
}
.logo_text {
  font-family: "Roboto Slab", serif;
  font-size: 36px;
  font-weight: 700;
  line-height: 0.75;
  color: #384158;
  vertical-align: middle;
  margin-left: 7px;
  -webkit-transition: all 200ms ease;
  transition: all 200ms ease;
}
.logo_text span {
  color: #14bdee;
}
.header.scrolled .logo {
  width: 40px;
  height: 40px;
}
.header.scrolled .logo_text {
  font-size: 24px;
}

/*********************************
3.4 Main Nav
*********************************/

/* .main_nav_contaner {
} */
.main_nav,
.search_button,
.user {
  display: inline-block;
}
.main_nav li {
  display: inline-block;
  position: relative;
}
.main_nav li:not(:last-child) {
  margin-right: 44px;
}
.main_nav li a {
  font-size: 18px;
  font-weight: 500;
  color: #384158;
  -webkit-transition: all 200ms ease;
  transition: all 200ms ease;
}
.main_nav li a:hover,
.main_nav li a.active {
  color: #39c9f6;
  border-bottom: solid 2px #39c9f6;
}
.main_nav li.active::after {
  display: block;
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 100%;
  height: 2px;
  background: #14bdee;
  content: "";
}
.search_button {
  margin-left: 46px;
  cursor: pointer;
}
.user {
  margin-left: 23px;
  cursor: pointer;
}
.search_button i,
.user i {
  font-size: 18px;
  color: #181818;
  -webkit-transition: all 200ms ease;
  transition: all 200ms ease;
}
.search_button:hover i,
.user:hover i {
  color: #14bdee;
}
.header_search_form {
  display: block;
  position: relative;
  width: 40%;
}
.header_search_container {
  position: absolute;
  bottom: 0px;
  left: 0px;
  width: 100%;
  background: #14bdee;
  z-index: -1;
  opacity: 0;
  -webkit-transition: all 400ms ease;
  transition: all 400ms ease;
}
.header_search_container.active {
  bottom: -73px;
  opacity: 1;
}
.header_search_content {
  width: 100%;
  height: 73px;
}
.search_input {
  width: 100%;
  height: 40px;
  border: none;
  outline: none;
  padding-left: 20px;
}
.header_search_button {
  position: absolute;
  top: 0;
  right: 0;
  width: 40px;
  height: 100%;
  border: none;
  outline: none;
  cursor: pointer;
}

/*********************************
3.5 Hamburger
*********************************/

/* .hamburger_container {
} */
.hamburger {
  display: none;
  cursor: pointer;
}
.hamburger i {
  font-size: 20px;
  color: #353535;
  -webkit-transition: all 200ms ease;
  transition: all 200ms ease;
}
.hamburger:hover i {
  color: #14bdee;
}

/*********************************
4. Menu
*********************************/

.menu {
  position: fixed;
  top: 0;
  right: -400px;
  width: 400px;
  height: 100vh;
  background: #ffffff;
  z-index: 101;
  padding: 87px 40px;
  overflow: scroll;
}
.menu .logo a {
  color: #000000;
}
.menu.active {
  right: 0;
}
.menu_close_container {
  position: absolute;
  top: 40px;
  right: 40px;
  width: 18px;
  height: 18px;
  -webkit-transform-origin: center center;
          transform-origin: center center;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  cursor: pointer;
}
.menu_close {
  width: 100%;
  height: 100%;
  -webkit-transform-style: preserve-3D;
          transform-style: preserve-3D;
}
.menu_close div {
  width: 100%;
  height: 2px;
  background: #232323;
  top: 8px;
  -webkit-transition: all 200ms ease;
  transition: all 200ms ease;
}
.menu_close div:last-of-type {
  -webkit-transform: rotate(90deg) translateX(-2px);
  transform: rotate(90deg) translateX(-2px);
  -webkit-transform-origin: center;
          transform-origin: center;
}
.menu_close:hover div {
  background: #937c6f;
}
.menu .logo {
  margin-bottom: 60px;
}
.menu_nav ul li {
  margin-bottom: 9px;
}
.menu_nav ul li a {
  font-family: "Roboto", sans-serif;
  font-size: 14px;
  text-transform: uppercase;
  color: rgba(0, 0, 0, 1);
  font-weight: 700;
  letter-spacing: 0.1em;
  -webkit-transition: all 200ms ease;
  transition: all 200ms ease;
}
.menu_nav ul li a:hover,
.menu_nav ul li a.active {
  color: #14bdee;
}
.menu .search {
  width: 100%;
  margin-bottom: 67px;
}
.search {
  display: inline-block;
  width: 400px;
  -webkit-transform: translateY(2px);
  transform: translateY(2px);
}
.menu .header_search_form {
  width: 100%;
}
.search form {
  position: relative;
}
.menu .search_input {
  width: 100%;
  height: 40px;
  background: rgba(0, 0, 0, 0.1);
  border-radius: 3px;
  border: none;
  outline: none;
  padding-left: 15px;
  color: rgba(0, 0, 0, 0.5);
}
.menu .search_input::-webkit-input-placeholder {
  font-family: "Roboto", sans-serif;
  font-size: 14px !important;
  font-weight: 400 !important;
  color: rgba(0, 0, 0, 0.4) !important;
}
.menu .search_input:-moz-placeholder {
  font-family: "Roboto", sans-serif;
  font-size: 14px !important;
  font-weight: 400 !important;
  color: rgba(0, 0, 0, 0.4) !important;
}
.menu .search_input::-moz-placeholder {
  font-family: "Roboto", sans-serif;
  font-size: 14px !important;
  font-weight: 400 !important;
  color: rgba(0, 0, 0, 0.4) !important;
}
.menu .search_input:-ms-input-placeholder {
  font-family: "Roboto", sans-serif;
  font-size: 14px !important;
  font-weight: 400 !important;
  color: rgba(0, 0, 0, 0.4) !important;
}
.menu .search_input::input-placeholder {
  font-family: "Roboto", sans-serif;
  font-size: 14px !important;
  font-weight: 400 !important;
  color: rgba(0, 0, 0, 0.4) !important;
}

/*********************************
5. Section
*********************************/

.section_title_container {
  max-width: 600px;
  margin: 0 auto;
}
.section_title {
  line-height: 1.2;
}
.section_subtitle {
  line-height: 1.85;
  margin-top: 14px;
}
.section_background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url(/LearnEnglish_React/FrontEnd/static/media/team_background.c798e70e.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}

/*********************************
6. Home
*********************************/

.home {
  width: 100%;
}
.home_slider_container {
  width: 100%;
  height: 100%;
}
.home_slider {
  width: 100%;
  height: 100%;
}
.home_slider_background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}
.home_slider_content {
  margin: auto;
  padding: 150px 0;
  width: 100%;
  height: auto;
}
.home_slider_title {
  font-family: "Roboto Slab", serif;
  font-size: 60px;
  font-weight: 700;
  line-height: 1.2;
  color: #384158;
}
.home_slider_subtitle {
  font-size: 24px;
  color: #76777a;
  margin-top: 9px;
}
.home_slider_form_container {
  width: 832px;
  margin: 0 auto;
  margin-top: 35px;
}
.home_search_form div {
  width: calc(100% - 140px);
  height: 46px;
  background: #ffffff;
  border-radius: 3px;
}
.home_search_input {
  width: 33.33333333%;
  border: none;
  outline: none;
  height: 25px;
  padding-left: 20px;
}
.home_search_input:not(:last-child) {
  border-right: solid 1px #e5e5e5;
}
.home_search_input::-webkit-input-placeholder {
  font-family: "Roboto", sans-serif;
  font-size: 14px !important;
  font-weight: 400 !important;
  color: #b5b8be !important;
}
.home_search_input:-moz-placeholder {
  font-family: "Roboto", sans-serif;
  font-size: 14px !important;
  font-weight: 400 !important;
  color: #b5b8be !important;
}
.home_search_input::-moz-placeholder {
  font-family: "Roboto", sans-serif;
  font-size: 14px !important;
  font-weight: 400 !important;
  color: #b5b8be !important;
}
.home_search_input:-ms-input-placeholder {
  font-family: "Roboto", sans-serif;
  font-size: 14px !important;
  font-weight: 400 !important;
  color: #b5b8be !important;
}
.home_search_input::input-placeholder {
  font-family: "Roboto", sans-serif;
  font-size: 14px !important;
  font-weight: 400 !important;
  color: #b5b8be !important;
}
.dropdown_item_select {
  position: relative;
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  -o-appearance: none;
  appearance: none;
  box-shadow: 0px 0px 0px rgba(0, 0, 0, 0);
  -webkit-box-shadow: 0px 0px 0px rgba(0, 0, 0, 0);
  -webkit-user-select: none;
  background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAACcAAAAFCAYAAADGx/UIAAAAGXRFWHRTb2Z0d2FyZQBBZG9iZSBJbWFnZVJlYWR5ccllPAAAAHRJREFUeNpi/P//PwO5ICwsWhBInQHi9NWrl+1BlgsNjVoFpM6uWrW0k1zzmRgoAEAHvQc5AIhXAR3jguYwF6gc2YCRkpBjZGREd0w6iAtlh4FCkyLzqeE4JAeCHPYe5jCQ+KBwHNSBHUBqD3L6o8R8gAADADOpOGT/f+xSAAAAAElFTkSuQmCC);
  background-position: center right;
  background-repeat: no-repeat;
  background-color: white;
  padding-right: 20px;
  padding-left: 20px;
}
.home_search_button {
  width: 120px;
  height: 46px;
  background: #14bdee;
  border: none;
  outline: none;
  border-radius: 3px;
  font-size: 14px;
  font-weight: 500;
  color: #ffffff;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  cursor: pointer;
}
.home_slider_nav {
  position: absolute;
  top: 55.8%;
  width: 50px;
  background: #b6b7bd99;
  border-radius: 50%;
  text-align: center;
  z-index: 10;
  cursor: pointer;
  -webkit-transition: all 200ms ease;
  transition: all 200ms ease;
}
.home_slider_prev {
  left: 75px;
}
.home_slider_next {
  right: 75px;
}
.home_slider_nav i {
  line-height: 50px;
  color: #ffffff;
  font-size: 22px;
}
.home_slider_nav:hover {
  background: #14bdee;
}

/*********************************
7. Features
*********************************/

.features {
  width: 100%;
  background: #ffffff;
  padding-top: 93px;
  padding-bottom: 103px;
}
.features_row {
  margin-top: 55px;
}
.feature {
  width: 100%;
  padding-top: 30px;
  padding-bottom: 28px;
  padding-left: 15px;
  padding-right: 15px;
  background: #ffffff;
}
.feature:hover {
  box-shadow: 0px 5px 40px rgba(29, 34, 47, 0.15);
}
.feature_icon {
  height: 55px;
}
.feature_icon img {
  max-width: 100%;
}
.feature_title {
  position: relative;
  font-size: 20px;
  margin-top: 23px;
}

/*********************************
8. Hit Courses
*********************************/

.hit_courses {
  width: 100%;
  padding-top: 93px;
  padding-bottom: 100px;
}
.courses_row {
  margin-top: 45px;
}
.course {
  display: block;
  width: 100%;
  border: none;
  text-align: left;
  cursor: pointer;
  border-radius: 6px;
  background: #ffffff;
  box-shadow: 0px 1px 5px rgba(29, 34, 47, 0.1);
}
.course:hover {
  box-shadow: 0px 5px 10px rgba(29, 34, 47, 0.3);
}
.course:hover .course_title {
  color: #14bdee;
}
.course_image {
  width: 100%;
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
  overflow: hidden;
}
.course_image img {
  max-width: 100%;
}
.course_body {
  padding-top: 22px;
  padding-left: 30px;
  padding-right: 30px;
  padding-bottom: 23px;
}
.course_title {
  font-family: "Roboto Slab", serif;
  font-size: 20px;
  color: #384158;
  -webkit-transition: all 200ms ease;
  transition: all 200ms ease;
}
.course_teacher {
  font-size: 15px;
  font-weight: 400;
  color: #384158;
  margin-top: 6px;
}
.course_text {
  margin-top: 13px;
}
.course_footer {
  padding-left: 30px;
  padding-right: 30px;
}
.course_footer_content {
  width: 100%;
  border-top: solid 1px #e5e5e5;
  padding-top: 9px;
  padding-bottom: 11px;
}
.course_info {
  display: inline-block;
  font-size: 14px;
  font-weight: 400;
  color: #55555a;
  margin: 5px;
}
.course_info i {
  color: #ffc80a;
}
.course_price {
  font-family: "Roboto Slab", serif;
  font-size: 20px;
  font-weight: 700;
  color: #14bdee;
}
.course_price span {
  font-family: "Roboto Slab", serif;
  font-size: 14px;
  font-weight: 400;
  text-decoration: line-through;
  color: #b5b8be;
  margin-right: 10px;
}
.courses_button {
  width: 210px;
  height: 46px;
  border-radius: 3px;
  background: #14bdee;
  text-align: center;
  margin: 0 auto;
  margin-top: 41px;
  box-shadow: 0px 5px 40px rgba(29, 34, 47, 0.15);
}
.courses_button:hover {
  box-shadow: 0px 5px 40px rgba(29, 34, 47, 0.45);
}
.courses_button a {
  display: block;
  font-size: 14px;
  font-weight: 500;
  text-transform: uppercase;
  line-height: 46px;
  color: #ffffff;
}

/*********************************
9. Counter
*********************************/

.counter {
  width: 100%;
  background: #ffffff;
  z-index: 2;
}
.counter_background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url(/LearnEnglish_React/FrontEnd/static/media/counter_background.50c83a2a.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}
.counter_content {
  padding-top: 119px;
  padding-bottom: 125px;
}
.counter_title {
  font-family: "Roboto Slab", serif;
  color: #ffffff;
  font-weight: 700;
}
.counter_text {
  margin-top: 19px;
}
.counter_text p {
  color: #ffffff;
}
.milestones {
  margin-top: 39px;
}
.milestone {
  text-align: center;
}
.milestone:not(:last-child)::after {
  display: block;
  position: absolute;
  top: 0;
  right: -45px;
  width: 1px;
  height: 70px;
  background: rgba(255, 255, 255, 0.2);
  content: "";
  -webkit-transform: rotate(30deg);
  transform: rotate(30deg);
}
.milestone_counter {
  font-size: 42px;
  font-weight: 700;
  line-height: 0.75;
  color: #14bdee;
}
.milestone_text {
  font-size: 16px;
  font-weight: 400;
  color: #ffffff;
  text-transform: uppercase;
  margin-top: 14px;
}
.counter_form {
  position: absolute;
  top: 0;
  right: 30px;
  width: 380px;
  height: 100%;
  background: #ffffff;
  padding-left: 40px;
  padding-right: 40px;
